<template>
    <div class="screen">
        <Title :title="translate.TR_TITLE_UPDATE" :subtitle="translate.TR_SUBTITLE_UPDATE" />
        <div class="body">  
            <div class="loader" v-if="getProps.isLoading"></div>
            <div class="scroller" v-else>
                <div id="form-create">
                    <div class="form">
                        <p>P</p>
                        <input type="text" v-model="getProps.prop" v-on:change="sendRecreate" :placeholder="translate.TR_CREATE_INPUT_PROP">
                    </div>
                    <div class="form">
                        <p>X</p>
                        <input type="number" step="0.1" v-on:input="sendUpdate" v-model="getProps.pos_x" :placeholder="translate.TR_CREATE_POS_X">
                    </div>
                    <div class="form">
                        <p>Y</p>
                        <input type="number" step="0.1" v-on:input="sendUpdate" v-model="getProps.pos_y" :placeholder="translate.TR_CREATE_POS_Y">
                    </div>
                    <div class="form">
                        <p>Z</p>
                        <input type="number" step="0.1" v-on:input="sendUpdate" v-model="getProps.pos_z" :placeholder="translate.TR_CREATE_POS_Z">
                    </div>
                    <div class="form">
                        <p>H</p>
                        <input type="number" step="0.1" v-on:input="sendUpdate" v-model="getProps.heading" :placeholder="translate.TR_CREATE_HEADING">
                    </div>
                </div>
                <div id="buttons-create">
                    <div>
                        <p v-on:click="getInfoPerson">{{translate.TR_CREATE_BUTTON_PERSON}}</p>
                        <p v-on:click="savaUpdateProp">{{translate.TR_CREATE_BUTTON_UPDATE}}</p>
                        <p v-on:click="cancelUpdate">{{translate.TR_CREATE_BUTTON_BACK}}</p>
                    </div>                
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>

import Title from '../components/Title';

export default {
    name: "Create",
    components:{
        Title
    },
    props:{
        getProps:{

        },
        OpenScreen:{

        },
        NameResource:{

        },
        translate: {
            
        },
        close:{
            
        }
    },

    data: function(){
        return({      
        })
    },
    methods:{
        openScreen: function(screen){
            this.OpenScreen(screen);
        },

        sendUpdate: function(){
            if(this.getProps.pos_x && this.getProps.pos_y && this.getProps.pos_z && this.getProps.heading){
                
                fetch(`https://${this.NameResource}/updateProp`, {
                    method: 'POST',
                    body: JSON.stringify({
                        id      : this.getProps.id,
                        pos_x   : this.getProps.pos_x,
                        pos_y   : this.getProps.pos_y,
                        pos_z   : this.getProps.pos_z,
                        heading : this.getProps.heading
                    })
                }); 
            }
        },
        sendRecreate: function(){
            if(this.getProps.prop.length > 0 && this.getProps.pos_x && this.getProps.pos_y && this.getProps.pos_z && this.getProps.heading){
                fetch(`https://${this.NameResource}/recreateProp`, {
                    method: 'POST',
                    body: JSON.stringify({
                        id      : this.getProps.id,
                        prop    : this.getProps.prop,
                        pos_x   : this.getProps.pos_x,
                        pos_y   : this.getProps.pos_y,
                        pos_z   : this.getProps.pos_z,
                        heading : this.getProps.heading
                    })
                }); 
            }
        },
        getInfoPerson: function(){
            
            fetch(`https://${this.NameResource}/getInfoPerson`, {
                method: 'POST',
                body: JSON.stringify({})
            });  
        },

        savaUpdateProp: function(){
            if(this.getProps.prop.length > 0 && this.getProps.pos_x && this.getProps.pos_y && this.getProps.pos_z && this.getProps.heading){
                fetch(`https://${this.NameResource}/savaUpdateProp`, {
                    method: 'POST',
                    body: JSON.stringify({
                        id      : this.getProps.id,
                        prop    : this.getProps.prop,
                        pos_x   : this.getProps.pos_x,
                        pos_y   : this.getProps.pos_y,
                        pos_z   : this.getProps.pos_z,
                        heading : this.getProps.heading
                    })
                });  

                this.OpenScreen('Home');
                this.close();
            }
        },

        cancelUpdate: function(){
            fetch(`https://${this.NameResource}/cancelUpdate`, {
                method: 'POST',
                body: JSON.stringify({
                    id      : this.getProps.id,
                    prop    : this.getProps.prop,
                    pos_x   : this.getProps.pos_x,
                    pos_y   : this.getProps.pos_y,
                    pos_z   : this.getProps.pos_z,
                })
            });  

            this.openScreen('Home');
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.element){
                this.getProps.id        = lua.element.id;
                this.getProps.prop      = lua.element.prop;
                this.getProps.pos_x     = lua.element.pos_x;
                this.getProps.pos_y     = lua.element.pos_y;
                this.getProps.pos_z     = lua.element.pos_z;
                this.getProps.heading   = lua.element.heading;
                this.getProps.isLoading = false;
            }
            else if(lua.player_info){
                this.getProps.pos_x   = lua.player_info.pos_x;
                this.getProps.pos_y   = lua.player_info.pos_y;
                this.getProps.pos_z   = lua.player_info.pos_z;
                this.getProps.heading = lua.player_info.heading;

                fetch(`https://${this.NameResource}/updateProp`, {
                    method: 'POST',
                    body: JSON.stringify({
                        id      : this.getProps.id,
                        pos_x   : this.getProps.pos_x,
                        pos_y   : this.getProps.pos_y,
                        pos_z   : this.getProps.pos_z,
                        heading : this.getProps.heading
                    })
                }); 
            }  
            else if(lua.backToCreate){
                this.OpenScreen('Create');
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
} 
</script>

<style>
    .scroller{
        max-height: 55vh;
        overflow-y: scroll;
    }
    #form-create{
        width: 80%;
        margin: auto;
        margin-top: 2vh;        
    }
    .form{
        display: flex;
        flex-direction: row;

        padding: 0.2vh 1vh;

        align-items: center;
        margin-bottom: 1vh;
    }
    .form p{
        padding: 1vh;
        padding-right: 2vh;
        padding-left: 0;
    }
    .form input{
        flex: 1;

        border: none;
        outline: none;
    }
    
    #buttons-create div{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        margin-bottom: 2vh;
    }
</style>