<template>
    <div class="screen">
        <Title :title="translate.TR_TITLE_CREATE" :subtitle="translate.TR_SUBTITLE_CREATE" />
        <div class="body">  
            <div class="scroller">
                <div id="form-create">
                    <div class="form">
                        <p>P</p>
                        <input type="text" v-model="prop" :placeholder="translate.TR_CREATE_INPUT_PROP">
                    </div>
                    <div class="form">
                        <p>X</p>
                        <input type="number" step="0.1" v-model="pos_x" :placeholder="translate.TR_CREATE_POS_X">
                    </div>
                    <div class="form">
                        <p>Y</p>
                        <input type="number" step="0.1" v-model="pos_y" :placeholder="translate.TR_CREATE_POS_Y">
                    </div>
                    <div class="form">
                        <p>Z</p>
                        <input type="number" step="0.1" v-model="pos_z" :placeholder="translate.TR_CREATE_POS_Z">
                    </div>
                    <div class="form">
                        <p>H</p>
                        <input type="number" step="0.1" v-model="heading" :placeholder="translate.TR_CREATE_HEADING">
                    </div>
                   
                </div>
                <div id="buttons-create">
                    <div>
                        <p v-on:click="getInfoPerson">{{translate.TR_CREATE_BUTTON_PERSON}}</p>
                        <p v-on:click="createProp">{{translate.TR_CREATE_BUTTON_CREATE}}</p>
                        <p v-on:click="openScreen('Home')">{{translate.TR_CREATE_BUTTON_BACK}}</p>
                    </div>                
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import Title from '../components/Title';

export default {
    name: "Create",
    props:{
        OpenScreen:{

        },
        translate: {
               
        },

        NameResource :{
            
        }
    },
    components:{
        Title
    },

    data: function(){
        return({      
            prop    : '',
            pos_x   : '',
            pos_y   : '',
            pos_z   : '',
            heading : '',            
        })
    },
    methods:{
        getInfoPerson: function(){
            
            fetch(`https://${this.NameResource}/getInfoPerson`, {
                method: 'POST',
                body: JSON.stringify({})
            });  
        },

        createProp: function(){
            if(this.prop.length > 0 && this.pos_x && this.pos_y && this.pos_z && this.heading){
                this.OpenScreen('Update', {isLoading:true});
                fetch(`https://${this.NameResource}/createProp`, {
                    method: 'POST',
                    body: JSON.stringify({
                        prop    : this.prop,
                        pos_x   : this.pos_x,
                        pos_y   : this.pos_y,
                        pos_z   : this.pos_z,
                        heading : this.heading
                    })
                });  
            }
            
        },
        openScreen: function(screen){
            this.OpenScreen(screen);
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.player_info){
                this.pos_x   = lua.player_info.pos_x;
                this.pos_y   = lua.player_info.pos_y;
                this.pos_z   = lua.player_info.pos_z;
                this.heading = lua.player_info.heading;
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
} 
</script>

<style>
    .scroller{
        max-height: 55vh;
        overflow-y: scroll;
    }
    #form-create{
        width: 80%;
        margin: auto;
        margin-top: 2vh;        
    }
    .form{
        display: flex;
        flex-direction: row;

        padding: 0.2vh 1vh;

        align-items: center;
        margin-bottom: 1vh;
    }
    .form p{
        padding: 1vh;
        padding-right: 2vh;
        padding-left: 0;
    }
    .form input{
        flex: 1;

        border: none;
        outline: none;
    }
    
    #buttons-create div{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        margin-bottom: 2vh;
    }
</style>