<template>
    <div class="screen">
        <Title :title="translate.TR_TITLE_ELEMENT" :subtitle="getProps.prop" />
        <div class="body">  
            <div class="loader" v-if="isLoading"></div>
            <div v-else>
            <div class="options-update"><p v-on:click="changeStatus">{{getProps.status == 0 ? translate.TR_ELEMENT_STATUS_ON : translate.TR_ELEMENT_STATUS_OFF }}</p></div>
            <div class="options-update" v-if="getProps.status == 1"><p  v-on:click="openScreen('Update', getProps)">{{translate.TR_ELEMENT_UPDATE}}</p></div>
            <div class="options-update"><p v-on:click="deleteProp">{{translate.TR_ELEMENT_DELETE}}</p></div>
            <div class="options-update"><p v-on:click="teleportToProp">{{translate.TR_ELEMENT_TELEPORT}}</p></div>
            <div class="options-update"><p v-on:click="cancel">{{translate.TR_ELEMENT_CANCEL}}</p></div>
            </div>
        </div>
    </div>
</template>

<script>

import Title from '../components/Title';

export default {
    name: "Element",
    props:{
        element:{

        },
        getProps:{

        },
        OpenScreen:{

        },
        translate: {
                
        },

        NameResource: {

        },
        close:{
            
        }
    },
    components:{
        Title
    },

    data: function(){
        return({         
            isLoading: false,   
        })
    },
    methods: {
        openScreen: function(screen, props = null){
            this.OpenScreen(screen, props);
        },

        teleportToProp: function(){
            
            fetch(`https://${this.NameResource}/teleportToProp`, {
                method: 'POST',
                body: JSON.stringify({
                    pos_x   : this.getProps.pos_x,
                    pos_y   : this.getProps.pos_y,
                    pos_z   : this.getProps.pos_z
                })
            });
        },
        changeStatus: function(){
            fetch(`https://${this.NameResource}/changeStatus`, {
                method: 'POST',
                body: JSON.stringify({
                    id      : this.getProps.id,
                    prop    : this.getProps.prop,
                    status  : this.getProps.status == 0 ? 1 : 0,
                    pos_x   : this.getProps.pos_x,
                    pos_y   : this.getProps.pos_y,
                    pos_z   : this.getProps.pos_z,
                    heading : this.getProps.heading
                })
            });
            this.isLoading = true;
        },
        cancel: function(){
            this.OpenScreen('Home');
        },
        deleteProp: function(){
            fetch(`https://${this.NameResource}/deleteProp`, {
                method: 'POST',
                body: JSON.stringify({
                    id      : this.getProps.id
                })
            });

            this.OpenScreen('Home');
            this.close();
        },
        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.changeStatus){
                if(lua.id == this.getProps.id){
                    this.isLoading = false;
                    this.getProps.status = lua.status;
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
} 
</script>

<style>
    .options-update{
        width: 70%;
        margin: auto;
        margin-top: 1vh;
    }

    .options-update:first-child{
        margin-top: 5vh;
    }

    .options-update p{
        padding: 1vh 0;
    }
</style>