<template>
    <div :class="{App : !openned}">
        <div id="key" v-on:click="hideMouse">{{mouseON ? translate.TR_CLICK_HERE : translate.TR_PRESS_KEY}}<br>{{mouseON ? translate.TR_TO_CAM : translate.TR_TO_USE}}</div>
        <div id="App">
            <div id="exit" v-on:click="close">X</div>
            <Home v-if="page === 'Home'" :OpenScreen="openScreen" :list_props="list_props" :translate="translate" :NameResource="NameResource" />
            <Create v-if="page === 'Create'" :OpenScreen="openScreen" :translate="translate" :NameResource="NameResource" />
            <Element v-if="page === 'Element'" :OpenScreen="openScreen" :getProps="sendProps" :translate="translate" :NameResource="NameResource" :close="close" />
            <Update v-if="page === 'Update'" :OpenScreen="openScreen" :getProps="sendProps" :translate="translate" :NameResource="NameResource" :close="close"/>
        </div>
    </div>
</template>

<script>
import Home from './screens/Home';
import Create from './screens/Create';
import Element from './screens/Element';
import Update from './screens/Update';

export default {
    name: "App",
    components: {
        Home,
        Create,
        Update,
        Element
    },

    data: function(){
        return({
            page: "Home",

            sendProps: null,

            list_props: [
                {id: 1, prop: "cs_televisio2naaaaaaaaaaaaaaaaaaaaaaaaa", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 1},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 2, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0},
                {id: 10, prop: "cs_television", pos_x: 250.5, pos_y: 150.0, pos_z: 2.0, rot_x: 0.0, rot_y: 0.0, rot_z: 0.0, status: 0}
            ],

            translate: {
                TR_TITLE_HOME       : "PROPS",
                TR_SUBTITLE_HOME    : "Todos os props spawnados",
                TR_ORDER_TEXT       : "ordenado por index",
                TR_ORDER_SUBTEXT    : "total criados:",
                TR_STATUS_ON        : "ativado",
                TR_STATUS_OFF       : "desativado",

                TR_TITLE_CREATE         : "PROPS",
                TR_SUBTITLE_CREATE      : "Criar prop",
                TR_TITLE_UPDATE         : "PROPS",
                TR_SUBTITLE_UPDATE      : "Atualizar prop",
                TR_CREATE_PROP          : "Prop",
                TR_CREATE_INPUT_PROP    : "Digite um Prop Name",
                TR_CREATE_POS_X         : "Posio X do Prop",
                TR_CREATE_POS_Y         : "Posio Y do Prop",
                TR_CREATE_POS_Z         : "Posio Z do Prop",
                TR_CREATE_HEADING       : "Heading do Prop",
                TR_CREATE_BUTTON_PERSON : "Pegar do meu personagem",
                TR_CREATE_BUTTON_CREATE : "Criar",
                TR_CREATE_BUTTON_UPDATE : "Salvar",
                TR_CREATE_BUTTON_BACK   : "Voltar",

                TR_TITLE_ELEMENT        : "PROPS",
                TR_ELEMENT_STATUS_ON    : "Ativar",
                TR_ELEMENT_STATUS_OFF   : "Desativar",
                TR_ELEMENT_UPDATE       : "Atualizar",
                TR_ELEMENT_DELETE       : "Delete",
                TR_ELEMENT_TELEPORT     : "Teleportar para ele",
                TR_ELEMENT_CANCEL       : "Cancelar",

                TR_CLICK_HERE           : "Clique aqui",
                TR_PRESS_KEY            : "Pressione F5",
                TR_TO_USE               : "Para utilizar o mouse",
                TR_TO_CAM               : "Para se mover"

            },

            NameResource : "spawn_props",
            openned : false,
            mouseON : true,
        })
    },
    methods: {
        openScreen: function(name, props = null){
            this.sendProps = props;
            this.page = name;   
            
        },
        close: function(){
            fetch(`https://${this.NameResource}/close`, {
                method: 'POST',
                body: JSON.stringify({})
            });

            this.openned = false;
        },
        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.page){
                this.page = lua.page;
            }

            else if(lua.list_props){
                this.list_props = lua.list_props;

                this.openned = true;
            }
            else if(lua.mouseEnabled){
                this.mouseON = lua.mouseEnabled
            }

            else if(lua.configs){
                this.translate = lua.translate;
                this.NameResource = lua.NameResource;
            }
        },
        hideMouse: function(){
            fetch(`https://${this.NameResource}/disableMouse`, {
                method: 'POST',
                body: JSON.stringify({})
            });
            
            this.mouseON = false;
        },
        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
               this.close();
            }
        }

    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

